import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import styles from "./Breadcrumb.module.scss";
import arrow from "./assets/arrow.svg";

import MapStore from "../../../../stores/MapStore";
import AppStore from "../../../../stores/AppStore";

type BreadcrumbProps = {
  MapStore?: MapStore;
  AppStore?: AppStore;
};

/**
 *  - 
 */
@inject("AppStore", "MapStore")
@observer
class Breadcrumb extends PureComponent<BreadcrumbProps> {
  render() {
    const { MapStore } = this.props;
    const { level, currentFO, currentRegion, backToFO, reset } = MapStore;

    const NavCountry = () => {
      if (level === 0) {
        return (
          <li className={styles.breadcrumb__title}> </li>
        );
      } else {
        return (
          <li>
            <span className={styles.breadcrumb__link} onClick={reset}>
               
            </span>
          </li>
        );
      }
    };

    const NavFO = () => {
      if (level === 0) {
        return null;
      } else if (level === 1) {
        return (
          <li>
            <img src={arrow} alt="" className={styles.breadcrumb__arrow} />
            {currentFO.properties.name}
          </li>
        );
      } else {
        return (
          <li>
            <img src={arrow} alt="" className={styles.breadcrumb__arrow} />
            <span
              className={styles.breadcrumb__link}
              onClick={() => backToFO()}
            >
              {currentFO.properties.name}
            </span>
          </li>
        );
      }
    };
    const NavRegion = () =>
      level === 2 ? (
        <li>
          <img src={arrow} alt="" className={styles.breadcrumb__arrow} />
          {currentRegion.properties.name}
        </li>
      ) : null;

    return (
      <div className={styles.breadcrumb}>
        <ul>
          <NavCountry />
          <NavFO />
          <NavRegion />
        </ul>
      </div>
    );
  }
}

export default Breadcrumb;
