import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";
import { Line } from "rc-progress";

import styles from "./DetailedParticipants.module.scss";

import { ReactComponent as BackIcon } from "./assets/back.svg";

import ParticipantsStore from "../../stores/ParticipantsStore";
import AppStore from "../../stores/AppStore";

type DetailedParticipantsProps = {
  dataInfo: any;
  ParticipantsStore?: ParticipantsStore;
  AppStore?: AppStore;
};

/**
 *  -   
 */
@inject("AppStore", "ParticipantsStore")
@observer
class DetailedParticipants extends PureComponent<DetailedParticipantsProps> {
  render() {
    const { dataInfo } = this.props;
    const { AppStore, ParticipantsStore } = this.props;
    const { OSNcheck, changeOSN, changeSRN } = ParticipantsStore;
    const { participants } = AppStore.data;

    const value = participants.parts.find((part) =>
      OSNcheck ? part.isOsno : !part.isOsno
    ).value;

    return (
      <div className={styles.container}>
        <div className={styles.containerTitle}>
          <div className={styles.containerBack}>
            <BackIcon
              className={styles.icons}
              onClick={() => {
                dataInfo.title === "  "
                  ? changeOSN()
                  : changeSRN();
              }}
            />
            <span className={styles.title}>{dataInfo.title}</span>
          </div>
          <span className={styles.count}>{value}</span>
        </div>
        <div className={styles.containerDetailedInfo}>
          <div className={styles.detailedInfo}>
            <div className={styles.containerProgressBarLines}>
              <span className={styles.progressBarTitle}></span>
              <span className={styles.progressBarCost}>
                {dataInfo.importers}
              </span>
              <Line
                percent={dataInfo.importersPercent}
                strokeWidth={2.2}
                trailWidth={2.2}
                strokeColor="#8300FF"
                trailColor="rgba(21, 24, 29, 0.75)"
                className={styles.progressLine}
              />
            </div>
            <div className={styles.containerProgressBarLines}>
              <span className={styles.progressBarTitle}></span>
              <span className={styles.progressBarCost}>
                {dataInfo.exporters}
              </span>
              <Line
                percent={dataInfo.exportersPercent}
                strokeWidth={2.5}
                trailWidth={2.5}
                strokeColor="#DC39D5"
                trailColor="rgba(21, 24, 29, 0.75)"
                className={styles.progressLine}
              />
            </div>
          </div>
          <div className={styles.detailedInfo}>
            <div className={styles.containerProgressBarLines}>
              <span className={styles.progressBarTitle}> </span>
              <span className={styles.progressBarCost}>
                {dataInfo.insideTurn}
              </span>
              <Line
                percent={dataInfo.insideTurnPercent}
                strokeWidth={2.2}
                trailWidth={2.2}
                strokeColor="#FF6B00"
                trailColor="rgba(21, 24, 29, 0.75)"
                className={styles.progressLine}
              />
            </div>
            <div className={styles.containerProgressBarLines}>
              <span className={styles.progressBarTitle}></span>
              <span className={styles.progressBarCost}>
                {dataInfo.consumers}
              </span>
              <Line
                percent={dataInfo.consumersPercent}
                strokeWidth={2.5}
                trailWidth={2.5}
                strokeColor="#7E5B00"
                trailColor="rgba(21, 24, 29, 0.75)"
                className={styles.progressLine}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default DetailedParticipants;
