import { configure, observable, action, decorate } from "mobx";
import AppStore from "./AppStore";

import { geoPath, geoMercator, geoCentroid } from "d3-geo";
import data from "../components/Map/json/data";

configure({ enforceActions: "observed" });

class MapStore {
  appStore: AppStore;

  constructor(appStore: AppStore) {
    this.appStore = appStore;
  }

  width = 1020;
  height = 905;
  center: any = [92, 60];
  zoom = 2.8;
  level = 0;
  currentFO = null;
  currentFOCenter = null;
  currentFOZoom = null;
  currentRegion = null;
  data = data;
  diffs = [
    [99, 2, 8],
    [1, -3, 2.5],
    [2, -4, 2],
    [4, -4, 3],
    [5, 2, 5],
    [8, 3, -2],
    [77, 9, -3.7],
    [78, -20, 4],
  ];

  setHeight = (height): void => {
    this.height = height;
    this.center = [93, 61];
  };

  projection = (): any =>
    geoMercator()
      .rotate([-11, 0])
      .translate([800 / 2, (800 * 1.35) / 2])
      .precision(0.1);

  getSizeColor = (maxValue, minValue, value: number): any => {
    const step = (maxValue - minValue) / 6;

    if (value >= maxValue - step) {
      return [144, "#94A3B8"];
    } else if (value >= maxValue - 2 * step) {
      return [132, "#7B8DA7"];
    } else if (value >= maxValue - 3 * step) {
      return [120, "#647896"];
    } else if (value >= maxValue - 4 * step) {
      return [114, "#4E6383"];
    } else if (value >= maxValue - 5 * step) {
      return [90, "#3F5069"];
    } else if (value >= minValue) {
      return [82, "#2F3B4E"];
    }

    return [100, "#455369"];
  };

  handleGeographyClick = (geography: any, isToFO: boolean) => {
    const path = geoPath().projection(this.projection());
    const centroid = geoCentroid(geography);

    const bounds = path.bounds(geography);
    const dx = bounds[1][0] - bounds[0][0];
    const dy = bounds[1][1] - bounds[0][1];
    const zoom = 0.8 / Math.max(dx / this.width, dy / this.height);

    this.center = centroid;
    this.zoom = zoom;

    if (isToFO) {
      this.currentFOCenter = centroid;
      this.currentFOZoom = zoom;
    }
  };

  selectFO = (geo: any): void => {
    this.level = 1;
    this.currentFO = geo;
    this.appStore.setParams([
      ["district", geo.properties.foId],
      ["region", null],
    ]);
    this.handleGeographyClick(geo, true);
  };

  backToFO = (): void => {
    this.level = 1;
    this.appStore.setParams([["region", null]]);
    this.center = this.currentFOCenter;
    this.zoom = this.currentFOZoom;
  };

  selectRegion = (geo: any): void => {
    this.level = 2;
    this.currentRegion = geo;
    this.appStore.setParams([["region", geo.properties.regionId]]);
    this.handleGeographyClick(geo, false);
  };

  reset = (): void => {
    this.level = 0;
    this.currentFO = null;
    this.currentRegion = null;
    this.center = [93, 61];
    this.zoom = 2.8;
    this.appStore.setParams([
      ["district", null],
      ["region", null],
    ]);
  };
}

decorate(MapStore, {
  width: observable,
  height: observable,
  center: observable,
  zoom: observable,
  level: observable,
  currentFO: observable,
  currentRegion: observable,
  data: observable,
  diffs: observable,
  currentFOCenter: observable,
  currentFOZoom: observable,
  setHeight: action,
  handleGeographyClick: action,
  selectFO: action,
  backToFO: action,
  selectRegion: action,
  reset: action,
});

export default MapStore;
