import React, { PureComponent } from "react";
import Switch from "react-switch";
import { observer, inject } from "mobx-react";

import styles from "./BottomMenu.module.scss";

import { ReactComponent as RestoreFiltersIcon } from "./assets/restoreFilters.svg";
import { ReactComponent as MapIcon } from "./assets/map.svg";
import { ReactComponent as ListIcon } from "./assets/list.svg";

import AppStore from "../../stores/AppStore";

type BottomMenuProps = {
  AppStore?: AppStore;
};

type BottomMenuState = {
  //  ( : )
  checkedSwitchUnits: boolean;

  //  - 
  switchMap: boolean;
  //  - 
  switchList: boolean;
};

/**
 *  -  
 */
@inject("AppStore")
@observer
class BottomMenu extends PureComponent<BottomMenuProps, BottomMenuState> {
  constructor(props: BottomMenuProps) {
    super(props);
    this.state = {
      checkedSwitchUnits: false,

      switchMap: true,
      switchList: false,
    };
  }

  /**
   *    
   * @param checkedUnits -  
   * @private
   */
  _switchUnits = (checkedSwitchUnits: boolean): void => {
    this.setState({ checkedSwitchUnits });
  };

  render() {
    const {
      state: { checkedSwitchUnits, switchMap, switchList },
    } = this;

    const { AppStore } = this.props;
    const {
      periodType,
      setPeriodType,
      resetFilters,
      setUnitSize,
      unitSize,
    } = AppStore;

    return (
      <div className={styles.containerMenu}>
        <div className={styles.containerCounts}>
          <div className={styles.containerUnits}>
            <span
              className={unitSize === "Thousand" ? styles.activeSpan : ""}
              onClick={() => setUnitSize("Thousand")}
            >
              .
            </span>
            <span
              className={unitSize === "Million" ? styles.activeSpan : ""}
              onClick={() => setUnitSize("Million")}
            >
              
            </span>
            <span
              className={unitSize === "Billion" ? styles.activeSpan : ""}
              onClick={() => setUnitSize("Billion")}
            >
              
            </span>
          </div>
        </div>
        <div className={styles.containerSwitch}>
          <span
            className={`${
              checkedSwitchUnits ? styles.noActive : styles.active
            }`}
          >
            
          </span>
          <Switch
            checked={checkedSwitchUnits}
            onChange={this._switchUnits}
            offHandleColor="#3F71ED"
            onHandleColor="#3F71ED"
            offColor="#8B9CB8"
            onColor="#8B9CB8"
            handleDiameter={16}
            uncheckedIcon={false}
            checkedIcon={false}
            height={8}
            width={25}
          />
          <select
            className={`${styles.switchSelect} ${
              checkedSwitchUnits ? styles.active : styles.noActive
            }`}
          >
            <option></option>
            <option></option>
          </select>
        </div>
        <div className={styles.containerRestoreFilters}>
          <div className={styles.filters} onClick={resetFilters}>
            <RestoreFiltersIcon className={styles.icons} />
            <span> </span>
          </div>
        </div>
        <div className={styles.containerMap}>
          <div className={styles.containerUnits}>
            <span className={switchMap ? styles.activeSpan : ""}>
              <MapIcon
                className={styles.iconsMap}
                onClick={() => {
                  this.setState({
                    switchList: false,
                    switchMap: true,
                  });
                }}
              />
            </span>
            <span className={switchList ? styles.activeSpan : ""}>
              <ListIcon
                className={styles.iconsMap}
                onClick={() => {
                  this.setState({
                    switchList: true,
                    switchMap: false,
                  });
                }}
              />
            </span>
          </div>
        </div>
        <div className={styles.containerPeriods}>
          <div className={styles.containerUnits}>
            <span
              className={periodType === "year" ? styles.activeSpan : ""}
              onClick={() => setPeriodType("year")}
            >
               
            </span>
            <span
              className={periodType === "quarter" ? styles.activeSpan : ""}
              onClick={() => setPeriodType("quarter")}
            >
               
            </span>
            <span
              className={periodType === "month" ? styles.activeSpan : ""}
              onClick={() => setPeriodType("month")}
            >
               
            </span>
            <span
              className={periodType === "decade" ? styles.activeSpan : ""}
              onClick={() => setPeriodType("decade")}
            >
               
            </span>
          </div>
        </div>
      </div>
    );
  }
}

export default BottomMenu;
