import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import styles from "./GroupProduct.module.scss";
import DetailedLine from "./components/DetailedLine";

import GroupProductStore from "../../stores/GroupProductStore";
import AppStore from "../../stores/AppStore";

type GroupProductProps = {
  GroupProductStore?: GroupProductStore;
  AppStore?: AppStore;
};

/**
 *  -  
 */
@inject("AppStore", "GroupProductStore")
@observer
class GroupProduct extends PureComponent<GroupProductProps> {
  render() {
    const { AppStore } = this.props;

    const { goodsGroups } = AppStore.data;

    return (
      <div className={styles.container}>
        <div className={styles.containerTitle}>
          <span className={styles.title}> </span>

          {goodsGroups.map((item: any, index: any) => (
            <DetailedLine key={item.code} dataInfo={item} itemNumber={index} />
          ))}
        </div>
      </div>
    );
  }
}

export default GroupProduct;
