import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import styles from "./Period.module.scss";

import PeriodsStore from "../../../../stores/PeriodsStore";
import AppStore from "../../../../stores/AppStore";

type PeriodProps = {
  PeriodsStore?: PeriodsStore;
  AppStore?: AppStore;
  period: any;
};

/**
 *  - 
 */
@inject("PeriodsStore", "AppStore")
@observer
class Period extends PureComponent<PeriodProps> {
  render() {
    const { AppStore, PeriodsStore, period } = this.props;
    const { activeYear, selectYear } = PeriodsStore;

    const { unitSizeName } = AppStore;

    const barStyle: any = {
      height: `${(period.percent / 100) * 90}px`,
    };
    const periodClass: string =
      period.code === activeYear
        ? `${styles.period} ${styles.active}`
        : styles.period;

    return (
      <div key={period.code} className={styles.period__container}>
        <div className={periodClass} onClick={() => selectYear(period.code)}>
          <div className={styles.period__top}>
            <div className={styles.period__bar} style={barStyle}>
              <div className={styles.period__value}>
                &#8381;{" "}
                {period.value.toLocaleString("ru-RU", {
                  maximumFractionDigits: 2,
                })}{" "}
                {unitSizeName}
              </div>
            </div>
          </div>
          <div className={styles.period__date}>{period.code}</div>
        </div>
      </div>
    );
  }
}

export default Period;
