import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";
import { Line } from "rc-progress";

import styles from "./ProductTraceability.module.scss";

import { ReactComponent as TraceabilityIcon } from "./assets/traceability.svg";
import { ReactComponent as DropdownIcon } from "./assets/dropdown.svg";

import ProductTraceabilityStore from "../../stores/ProductTraceabilityStore";
import AppStore from "../../stores/AppStore";

type ProductTraceabilityProps = {
  ProductTraceabilityStore?: ProductTraceabilityStore;
  AppStore?: AppStore;
};

/**
 *  -   
 */
@inject("AppStore", "ProductTraceabilityStore")
@observer
class ProductTraceability extends PureComponent<ProductTraceabilityProps> {
  render() {
    const { AppStore, ProductTraceabilityStore } = this.props;
    const {
      changeIncludeTraceability,
      changeLeaveTraceability,
    } = ProductTraceabilityStore;

    const { unitSizeName } = AppStore;
    const { indicators } = AppStore.data;

    return (
      <div className={styles.container}>
        <div>
          <div className={styles.containerTitle}>
            <TraceabilityIcon className={styles.icons} />
            <span className={styles.title}>
              
              <br />
              
              <br />
              
            </span>
          </div>
          <div className={styles.containerTotalCost}>
            <span className={styles.titleTotalCost}>
              {" "}
              {(
                indicators.import.value +
                indicators.turnover.value +
                indicators.export.value
              ).toLocaleString("ru-RU", { maximumFractionDigits: 2 })}
            </span>
            <span className={styles.titleUnit}>{unitSizeName}</span>
          </div>
        </div>
        <div className={styles.containerProgressBar}>
          <div className={styles.containerProgressBarLines}>
            <div className={styles.containerProgressIcon}>
              <div className={styles.containerProgressTitle}>
                <span className={styles.progressBarTitle}>
                    
                </span>
                <span className={styles.progressBarCost}>
                  {" "}
                  {indicators.import.value.toLocaleString("ru-RU", {
                    maximumFractionDigits: 2,
                  })}{" "}
                  {unitSizeName}
                </span>
              </div>
              <DropdownIcon
                className={styles.iconDropdown}
                onClick={changeIncludeTraceability}
              />
            </div>
            <Line
              percent={indicators.import.percent}
              strokeWidth={2.2}
              trailWidth={2.2}
              strokeColor="#8300FF"
              trailColor="rgba(21, 24, 29, 0.75)"
              className={styles.progressLine}
            />
          </div>
          <div className={styles.containerProgressBarLines}>
            <span className={styles.progressBarTitle}></span>
            <span className={styles.progressBarCost}>
              {" "}
              {indicators.turnover.value.toLocaleString("ru-RU", {
                maximumFractionDigits: 2,
              })}{" "}
              {unitSizeName}
            </span>
            <Line
              percent={indicators.turnover.percent}
              strokeWidth={2}
              trailWidth={2}
              strokeColor="#21931F"
              trailColor="rgba(21, 24, 29, 0.75)"
              className={styles.progressLine}
            />
          </div>
          <div className={styles.containerProgressBarLines}>
            <div className={styles.containerProgressIcon}>
              <div className={styles.containerProgressTitle}>
                <span className={styles.progressBarTitle}>
                    
                </span>
                <span className={styles.progressBarCost}>
                  {" "}
                  {indicators.export.value.toLocaleString("ru-RU", {
                    maximumFractionDigits: 2,
                  })}{" "}
                  {unitSizeName}
                </span>
              </div>
              <DropdownIcon
                className={styles.iconDropdown}
                onClick={changeLeaveTraceability}
              />
            </div>
            <Line
              percent={indicators.export.percent}
              strokeWidth={2.2}
              trailWidth={2.2}
              strokeColor="#FF6B00"
              trailColor="rgba(21, 24, 29, 0.75)"
              className={styles.progressLine}
            />
          </div>
        </div>
      </div>
    );
  }
}

export default ProductTraceability;
