import React from "react";

import icons from "./assets/icons.svg";

type IconProps = {
  name: string;
  size?: string | number;
  height?: string | number;
  width?: string | number;
  fill?: string;
  [key: string]: any;
};

/**
 *  
 */
const Icon = ({ name, size, height, width, fill, ...props }: IconProps) => {
  return (
    <svg width={size || width} height={size || height} fill={fill} {...props}>
      <use href={`${icons}#${name}`} />
    </svg>
  );
};

Icon.defaultProps = {
  size: "",
  height: 12,
  width: 12,
};

export default Icon;
