import { configure, observable, action, decorate } from "mobx";
import AppStore from "./AppStore";

configure({ enforceActions: "observed" });

class ParticipantsStore {
  appStore: AppStore;

  constructor(appStore: AppStore) {
    this.appStore = appStore;
  }

  OSNcheck: boolean = false;
  SRNcheck: boolean = false;

  changeOSN = (): void => {
    this.OSNcheck = !this.OSNcheck;
    this.appStore.setParams([["osno", this.OSNcheck ? "true" : null]]);
  };

  changeSRN = (): void => {
    this.SRNcheck = !this.SRNcheck;
    this.appStore.setParams([["osno", this.SRNcheck ? "false" : null]]);
  };

  resetFilters = () => {
    this.OSNcheck = false;
    this.SRNcheck = false;
  };
}

decorate(ParticipantsStore, {
  OSNcheck: observable,
  SRNcheck: observable,
  changeOSN: action,
  changeSRN: action,
});

export default ParticipantsStore;
