import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import {
  ComposableMap,
  Geographies,
  Geography,
  ZoomableGroup,
  Marker,
} from "react-simple-maps";

import Breadcrumb from "./components/Breadcrumb";

import { geoCentroid } from "d3-geo";

import rusFederative from "./json/rus_federative.json";
import rusRegions from "./json/rus_regions.json";

import styles from "./Map.module.scss";

import KnIcon from "./assets/kn.svg";

import MapStore from "../../stores/MapStore";
import AppStore from "../../stores/AppStore";

type MapProps = {
  MapStore?: MapStore;
  AppStore?: AppStore;
};

/**
 *  - 
 */
@inject("AppStore", "MapStore")
@observer
class Map extends PureComponent<MapProps> {
  private mapContainerRef = React.createRef<HTMLDivElement>();

  componentDidMount() {
    const mapContainer: any = this.mapContainerRef.current;
    const mapHeight =
      (mapContainer.offsetHeight * 1020) / mapContainer.offsetWidth;
    this.props.MapStore.setHeight(mapHeight);
  }

  private onBundleMouseEnter = (e: any) => {
    const mapGroup: any = document.querySelector("#rsm-geographies");
    const currentBundle = e.currentTarget.parentNode;
    mapGroup.appendChild(currentBundle);

    const cuurentValue = currentBundle.querySelector(".bubble__value");
    cuurentValue.style.transform = `translate(0, ${
      1.5 / this.props.MapStore.zoom
    }%)`;

    const areas = mapGroup.querySelectorAll(".rsm-geography");
    const currentArea = mapGroup.querySelector(
      `path.${e.currentTarget.classList[2]}`
    );
    areas.forEach((area: any) => {
      area.style.opacity = 0.5;
      area.style.stroke = "rgba(199, 213, 236, 0.25)";
    });

    currentArea.style.opacity = 1;
    currentArea.style.stroke = "#44C8F5";
  };

  private onBundleMouseLeave = (e: any) => {
    const currentBundle = e.currentTarget.parentNode;
    const cuurentValue = currentBundle.querySelector(".bubble__value");
    const cuurentName = currentBundle.querySelector(".bubble__name");
    if (!cuurentName.classList.contains("visible"))
      cuurentValue.style.transform = `translate(0,0)`;

    const mapGroup: any = document.querySelector("#rsm-geographies");
    const areas = mapGroup.querySelectorAll(".rsm-geography");
    areas.forEach((area: any) => {
      area.style.opacity = 1;
      area.style.stroke = "rgba(199, 213, 236, 0.25)";
    });
  };

  private onAreaMouseEnter = (e: any) => {
    const mapGroup: any = document.querySelector("#rsm-geographies");
    const currentArea = e.currentTarget;
    const areas = mapGroup.querySelectorAll(".rsm-geography");

    areas.forEach((area: any) => {
      area.style.opacity = 0.5;
    });

    currentArea.style.opacity = 1;
    currentArea.style.stroke = "#44C8F5";
  };

  private onAreaMouseLeave = (e: any) => {
    const mapGroup: any = document.querySelector("#rsm-geographies");
    const areas = mapGroup.querySelectorAll(".rsm-geography");

    if (
      e.relatedTarget &&
      !e.relatedTarget.classList.contains(styles.bubble__circle)
    ) {
      areas.forEach((area: any) => {
        area.style.opacity = 1;
        area.style.stroke = "rgba(199, 213, 236, 0.25)";
      });
    }
  };

  render() {
    const { AppStore, MapStore } = this.props;
    const {
      width,
      height,
      center,
      zoom,
      level,
      currentFO,
      currentRegion,
      diffs,
    } = MapStore;

    const data =
      level === 0
        ? AppStore.data.districts || [
            {
              code: "0",
              value: 0,
            },
          ]
        : AppStore.data.regions || [
            {
              code: "0",
              value: 0,
            },
          ];

    const maxValue = [...data].sort((a: any, b: any) => b.value - a.value)[0]
      .value;
    const minValue = [...data].sort((a: any, b: any) => a.value - b.value)[0]
      .value;

    const type = level === 0 ? "foId" : "regionId";

    return (
      <div ref={this.mapContainerRef} className={styles.map__container}>
        <Breadcrumb />
        <div className={styles.kn}>
          <div className={styles.kn__icon}>
            <img src={KnIcon} alt="" />
          </div>
          <div className={styles.kn__text}>
            <div className={styles.kn__title}>    </div>
            <div className={styles.kn__value}>67,45</div>
          </div>
        </div>
        <div className={styles.map}>
          <ComposableMap
            projection={MapStore.projection()}
            width={width}
            height={height}
          >
            <ZoomableGroup
              center={center}
              zoom={zoom}
              maxZoom={150}
              className={styles.map__zoomable}
            >
              <Geographies
                geography={level === 0 ? rusFederative : rusRegions}
                id="rsm-geographies"
              >
                {({ geographies }) => (
                  <>
                    {geographies.map((geo) => {
                      if (
                        level === 0 ||
                        (level === 1 &&
                          geo.properties.foId === currentFO.properties.foId) ||
                        (level === 2 &&
                          geo.properties.regionId ===
                            currentRegion.properties.regionId)
                      ) {
                        const markerData = data.find(
                          (item: any) => item.code === geo.properties[type] + ""
                        ) || {
                          code: geo.properties[type],
                          value: 0,
                        };

                        return (
                          <Geography
                            key={geo.rsmKey}
                            geography={geo}
                            onMouseLeave={this.onAreaMouseLeave}
                            onMouseEnter={this.onAreaMouseEnter}
                            className={`${styles.map__area} region_${geo.properties.regionId}`}
                            onClick={() =>
                              level === 0
                                ? MapStore.selectFO(geo)
                                : MapStore.selectRegion(geo)
                            }
                            style={{
                              default: {
                                fill: MapStore.getSizeColor(
                                  maxValue,
                                  minValue,
                                  markerData.value
                                )[1],
                                outline: "none",
                                strokeWidth: 1 / zoom,
                                cursor: "pointer",
                              },
                              hover: {
                                fill: MapStore.getSizeColor(
                                  maxValue,
                                  minValue,
                                  markerData.value
                                )[1],
                                strokeWidth: 1 / zoom,
                                outline: "none",
                                cursor: "pointer",
                              },
                              pressed: {
                                fill: MapStore.getSizeColor(
                                  maxValue,
                                  minValue,
                                  markerData.value
                                )[1],
                                strokeWidth: 1 / zoom,
                                outline: "none",
                                cursor: "pointer",
                              },
                            }}
                          />
                        );
                      }
                      return null;
                    })}
                    {geographies.map((geo) => {
                      const diff =
                        level < 2
                          ? diffs.find(
                              (diff: any) => diff[0] === geo.properties.regionId
                            ) || [0, 0, 0]
                          : [0, 0, 0];
                      const centroid = geoCentroid(geo);
                      const center: any = [
                        centroid[0] + diff[1],
                        centroid[1] + diff[2],
                      ];

                      const markerData = data.find(
                        (item: any) => item.code === geo.properties[type] + ""
                      );

                      const valueStyle = {
                        transform:
                          geo.properties.regionId === 77 ||
                          geo.properties.regionId === 78
                            ? `translate(0, ${1.5 / zoom}%)`
                            : `translate(0,0)`,
                      };

                      const textVisible =
                        geo.properties.regionId === 77 ||
                        geo.properties.regionId === 78
                          ? `${styles.visible} visible`
                          : ``;

                      if (
                        (markerData && level === 0) ||
                        (markerData &&
                          level === 1 &&
                          geo.properties.foId === currentFO.properties.foId) ||
                        (markerData &&
                          level === 2 &&
                          geo.properties.regionId ===
                            currentRegion.properties.regionId)
                      ) {
                        return (
                          <g key={geo.rsmKey + "-name"}>
                            <Marker
                              coordinates={center}
                              className={`${styles.bubble} region_${geo.properties.regionId}`}
                              onClick={() =>
                                level === 0
                                  ? MapStore.selectFO(geo)
                                  : MapStore.selectRegion(geo)
                              }
                              onMouseEnter={this.onBundleMouseEnter}
                              onMouseLeave={this.onBundleMouseLeave}
                            >
                              <circle
                                r={
                                  MapStore.getSizeColor(
                                    maxValue,
                                    minValue,
                                    markerData.value
                                  )[0] /
                                  2 /
                                  zoom
                                }
                                strokeWidth={2 / zoom}
                                className={styles.bubble__circle}
                              />
                              <text
                                y={-(5 / zoom)}
                                fontSize={16 / zoom}
                                className={`${styles.bubble__name} bubble__name ${textVisible}`}
                              >
                                {geo.properties.name}
                              </text>
                              <text
                                y={7 / zoom}
                                fontSize={20 / zoom}
                                className={`${styles.bubble__value} bubble__value`}
                                style={valueStyle}
                              >
                                {markerData.value.toLocaleString("ru-RU", {
                                  maximumFractionDigits: 2,
                                })}
                              </text>
                            </Marker>
                          </g>
                        );
                      }
                      return null;
                    })}
                  </>
                )}
              </Geographies>
            </ZoomableGroup>
          </ComposableMap>
        </div>
      </div>
    );
  }
}

export default Map;
