import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import Period from "./components/Period";
import styles from "./Periods.module.scss";

import PeriodsStore from "../../stores/PeriodsStore";
import AppStore from "../../stores/AppStore";

type PeriodsProps = {
  PeriodsStore?: PeriodsStore;
  AppStore?: AppStore;
};

type PeriodsState = {
  activePeriod: number;
  data: any;
};

/**
 *  - 
 */
@inject("AppStore", "PeriodsStore")
@observer
class Periods extends PureComponent<PeriodsProps, PeriodsState> {
  render() {
    const { AppStore } = this.props;
    const { periods } = AppStore.data;

    return (
      <div className={styles.container}>
        {periods.periods.map((period: any, i: any) => (
          <Period key={i} period={period} />
        ))}
        <div
          className={`${styles.periods__line} ${styles.periods__line__1}`}
        ></div>
        <div
          className={`${styles.periods__line} ${styles.periods__line__2}`}
        ></div>
        <div
          className={`${styles.periods__line} ${styles.periods__line__3}`}
        ></div>
        <div
          className={`${styles.periods__line} ${styles.periods__line__4}`}
        ></div>
      </div>
    );
  }
}

export default Periods;
