import { observable, configure, action, decorate, computed } from "mobx";

import ProductTraceabilityStore from "./ProductTraceabilityStore";
import MapStore from "./MapStore";
import ParticipantsStore from "./ParticipantsStore";
import GroupProductStore from "./GroupProductStore";
import CountriesStore from "./CountriesStore";
import PeriodsStore from "./PeriodsStore";

configure({ enforceActions: "observed" });

class AppStore {
  //ProductTraceabilityStore: ProductTraceabilityStore = new ProductTraceabilityStore();
  ProductTraceabilityStore: ProductTraceabilityStore;
  MapStore: MapStore;
  ParticipantsStore: ParticipantsStore;
  GroupProductStore: GroupProductStore;
  CountriesStore: CountriesStore;
  PeriodsStore: PeriodsStore;

  constructor() {
    this.ProductTraceabilityStore = new ProductTraceabilityStore(this);
    this.MapStore = new MapStore(this);
    this.ParticipantsStore = new ParticipantsStore(this);
    this.GroupProductStore = new GroupProductStore(this);
    this.CountriesStore = new CountriesStore(this);
    this.PeriodsStore = new PeriodsStore(this);
    this.fetch();
  }

  theme: string = "light";
  params: any = {
    indicator: null,
  };

  data: any = {
    indicators: {
      import: {
        value: 0,
        percent: 0,
      },
      turnover: {
        value: 0,
        percent: 0,
      },
      export: {
        value: 0,
        percent: 0,
      },
    },
    participants: {
      full: 0,
      parts: [],
    },
    goodsGroups: [],
    countries: [],
    risks: [],
    districts: [
      {
        code: "0",
        value: 0,
      },
    ],
    regions: [
      {
        code: "0",
        value: 0,
      },
    ],
    periods: {
      periodType: "year",
      periods: [],
    },
  };

  fetch = () => {
    let params = "";
    const paramsKeys = Object.keys(this.params).filter(
      (key) => this.params[key] !== null
    );

    paramsKeys.forEach((key, index) => {
      if (this.params[key]) {
        if (index === 0) params += "?";
        params += `${key}=${this.params[key]}`;
        if (index !== paramsKeys.length - 1) params += "&";
      }
    });

    const responceStr = 'https://lesh1j.github.io/DashboardManager.json' + params;
    //const responceStr =
    //  "/inias/csc/gws/uc/TrcDashboardManagerApi/api/GetPageData" + params;

    fetch(responceStr, { method: "GET" })
      .then((res) => res.json())
      .then((json) => this.updateData(json));
  };

  setParams = (params: any) => {
    params.forEach((param) => (this.params[param[0]] = param[1]));
    this.fetch();
  };

  updateData = (data: any) => {
    this.data = data;
  };

  periodType: string = "year";

  setPeriodType = (type) => {
    this.periodType = type;
    //this.setParams([['tax_period', '']]);
  };

  unitSize: string = "Thousand";

  get unitSizeName(): string {
    switch (this.unitSize) {
      case "Thousand":
        return ".";
      case "Million":
        return ".";
      case "Billion":
        return ".";
      default:
        return ".";
    }
  }

  setUnitSize = (unitSize) => {
    this.unitSize = unitSize;
    this.setParams([["unit_size", unitSize]]);
  };

  resetFilters = () => {
    this.params = {};
    this.ProductTraceabilityStore.resetFilters();
    this.ParticipantsStore.resetFilters();
    this.GroupProductStore.resetFilters();
    this.CountriesStore.resetFilters();
    this.PeriodsStore.resetFilters();
    this.MapStore.reset();
    this.periodType = "year";
    this.unitSize = "Thousand";
    this.fetch();
  };
}

decorate(AppStore, {
  periodType: observable,
  data: observable,
  theme: observable,
  params: observable,
  unitSize: observable,
  unitSizeName: computed,
  setParams: action,
  fetch: action,
  updateData: action,
  setPeriodType: action,
  resetFilters: action,
  setUnitSize: action,
});

export default AppStore;
