import React, { PureComponent } from "react";

import styles from "./LeftMenu.module.scss";

import { ReactComponent as LogoIcon } from "./assets/logo.svg";
import { ReactComponent as CirculationIcon } from "./assets/circulation.svg";
import { ReactComponent as ResalesIcon } from "./assets/resales.svg";
import { ReactComponent as SearchIcon } from "./assets/search.svg";
import { ReactComponent as DarkThemeIcon } from "./assets/dark.svg";
import { ReactComponent as LightThemeIcon } from "./assets/light.svg";

type LeftMenuProps = {};

type LeftMenuState = {
  //  - 
  switchTurnover: boolean;
  //  - 
  switchResales: boolean;
  //  -    
  switchSearch: boolean;
};

/**
 *  -  
 */
class LeftMenu extends PureComponent<LeftMenuProps, LeftMenuState> {
  constructor(props: LeftMenuProps) {
    super(props);
    this.state = {
      switchTurnover: true,
      switchResales: false,
      switchSearch: false,
    };
  }

  render() {
    const {
      state: { switchTurnover, switchResales, switchSearch },
    } = this;

    return (
      <div className={styles.containerMenu}>
        <div className={styles.menu}>
          <div className={styles.logoIconContainer}>
            <LogoIcon className={styles.logoIcon} />
          </div>
          <div className={styles.iconsContainer}>
            <div
              className={
                switchTurnover
                  ? `${styles.menuItem} ${styles.activeMenu}`
                  : styles.menuItem
              }
              onClick={() => {
                this.setState({
                  switchTurnover: true,
                  switchResales: false,
                  switchSearch: false,
                });
              }}
            >
              <CirculationIcon className={styles.icons} />
              <span className={styles.labelUpper}></span>
            </div>
            <div
              className={
                switchResales
                  ? `${styles.menuItem} ${styles.activeMenu}`
                  : styles.menuItem
              }
              onClick={() => {
                this.setState({
                  switchTurnover: false,
                  switchResales: true,
                  switchSearch: false,
                });
              }}
            >
              <ResalesIcon className={styles.icons} />
              <span className={styles.labelUpper}></span>
            </div>
            <div
              className={
                switchSearch
                  ? `${styles.menuItem} ${styles.activeMenu}`
                  : styles.menuItem
              }
              onClick={() => {
                this.setState({
                  switchTurnover: false,
                  switchResales: false,
                  switchSearch: true,
                });
              }}
            >
              <SearchIcon className={styles.icons} />
              <span className={styles.labelLower}>
                
                <br />
                  
              </span>
            </div>
          </div>
          <div className={styles.themeContainer}>
            <DarkThemeIcon className={styles.iconsTheme} />
            <LightThemeIcon className={styles.iconsTheme} />
          </div>
        </div>
      </div>
    );
  }
}

export default LeftMenu;
