import { configure, observable, action, decorate } from "mobx";
import AppStore from "./AppStore";

configure({ enforceActions: "observed" });

class CountriesStore {
  appStore: AppStore;

  constructor(appStore: AppStore) {
    this.appStore = appStore;
  }

  filterAll: boolean = true;
  filterEAES: boolean = false;
  activeColor: string = "#3E516F";
  isOpen: boolean = false;

  showAllCountries = (): void => {
    this.filterEAES = false;
    this.filterAll = true;
    this.appStore.setParams([["eaeu", null]]);
  };

  showEAESCountries = (): void => {
    this.filterEAES = true;
    this.filterAll = false;
    this.appStore.setParams([["eaeu", true]]);
  };

  setOpen = (): void => {
    this.isOpen = true;
  };

  setClose = (): void => {
    this.isOpen = false;
  };

  resetFilters = () => {
    this.filterEAES = false;
    this.filterAll = true;
    this.isOpen = false;
  };
}

decorate(CountriesStore, {
  filterAll: observable,
  filterEAES: observable,
  activeColor: observable,
  isOpen: observable,
  showAllCountries: action,
  showEAESCountries: action,
  setOpen: action,
  setClose: action,
});

export default CountriesStore;
