import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";
import { PieChart } from "react-minimal-pie-chart";

import styles from "./Participants.module.scss";
import Icon from "../Icon";
import { ReactComponent as DropdownIcon } from "../ProductTraceability/assets/dropdown.svg";

import ParticipantsStore from "../../stores/ParticipantsStore";
import AppStore from "../../stores/AppStore";

type ParticipantsProps = {
  ParticipantsStore?: ParticipantsStore;
  AppStore?: AppStore;
};

/**
 *  -  
 */
@inject("AppStore", "ParticipantsStore")
@observer
class Participants extends PureComponent<ParticipantsProps> {
  render() {
    const { AppStore, ParticipantsStore } = this.props;
    const { changeOSN, changeSRN } = ParticipantsStore;
    const { participants } = AppStore.data;

    const preparedData = [...participants.parts].map((part) => {
      if (part.isOsno) {
        return {
          isOsno: part.isOsno,
          title: "",
          value: part.value,
          color: "#8300FF",
        };
      }
      return {
        isOsno: part.isOsno,
        title: "",
        value: part.value,
        color: "#FF6B00",
      };
    });

    return (
      <div className={styles.container}>
        <div className={styles.containerTitle}>
          <span className={styles.title}> </span>
          <span className={styles.count}>{participants.full}</span>
        </div>
        <div className={styles.containerChart}>
          <PieChart data={preparedData} radius={35} segmentsShift={1} />
        </div>
        <div className={styles.containerLegend}>
          {preparedData.map((item: any, index: any) => (
            <div key={item.value} className={styles.legend}>
              <div className={styles.legendName}>
                <div className={styles.containerIcons}>
                  <Icon
                    name="circle"
                    fill={item.color}
                    width={10}
                    height={10}
                    className={styles.circle}
                  />
                  <Icon
                    name="ellipse"
                    fill={item.color}
                    width={18}
                    height={18}
                    className={styles.ellipse}
                  />
                </div>
                {item.title}
              </div>
              <div className={styles.show}>
                <span className={styles.legendDescription}>
                  {item.value.toLocaleString("ru-RU", {
                    maximumFractionDigits: 2,
                  })}
                </span>
                <DropdownIcon
                  className={styles.iconDropdown}
                  onClick={() => {
                    item.isOsno ? changeOSN() : changeSRN();
                  }}
                />
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default Participants;
