import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";
import { Line } from "rc-progress";
import ReactTooltip from "react-tooltip";

import styles from "./DetailedLine.module.scss";
import { ReactComponent as DropdownIcon } from "../../assets/dropdown.svg";
import { ReactComponent as InfoIcon } from "../../assets/info.svg";
import { ReactComponent as BackIcon } from "../../assets/back.svg";

import GroupProductStore from "../../../../stores/GroupProductStore";

type DetailedLineProps = {
  GroupProductStore?: GroupProductStore;
  dataInfo: any;
  itemNumber: number;
};

/**
 *  -  
 */
@inject("GroupProductStore")
@observer
class DetailedLine extends PureComponent<DetailedLineProps> {
  render() {
    const { dataInfo, itemNumber, GroupProductStore } = this.props;
    const { openGroup, closeGroup, openedGroup, getColor } = GroupProductStore;

    const components = [
      {
        title: "8418102001",
        percent: 11,
      },
      {
        title: "8418102008",
        percent: 6,
      },
      {
        title: "8418102003",
        percent: 4,
      },
      {
        title: "8418102002",
        percent: 1.15,
      },
    ];

    return (
      <div>
        <div className={styles.containerProgressBarLines}>
          <div className={styles.containerProgressIcon}>
            <div className={styles.containerProgressTitle}>
              <span className={styles.progressBarTitle}>{dataInfo.name}</span>
              <span className={styles.progressBarCost}>
                {dataInfo.percent}%
              </span>
            </div>
            {dataInfo.code !== openedGroup ? (
              <DropdownIcon
                className={styles.iconDropdown}
                onClick={() => openGroup(dataInfo.code)}
              />
            ) : (
              <BackIcon className={styles.iconDropdown} onClick={closeGroup} />
            )}
          </div>
          <Line
            percent={dataInfo.percent}
            strokeWidth={2.2}
            trailWidth={2.2}
            strokeColor={getColor(itemNumber)}
            trailColor="rgba(21, 24, 29, 0.75)"
            className={styles.progressLine}
          />
        </div>
        <div>
          {dataInfo.code === openedGroup &&
            components.map((item: any, index: any) => (
              <div
                key={item.title}
                className={styles.containerProgressBarLines}
              >
                <div className={styles.containerProgressIcon}>
                  <div className={styles.containerProgressTitle}>
                    <span className={styles.progressBarTitle}>
                      {item.title}
                    </span>
                    <span className={styles.progressBarCost}>
                      {item.percent}%
                    </span>
                  </div>
                  <InfoIcon
                    className={styles.iconDropdown}
                    data-tip="React-tooltip"
                  />
                  <ReactTooltip
                    place="right"
                    type="dark"
                    effect="solid"
                    border={true}
                    borderColor="#8B9CB8"
                    arrowColor="#8B9CB8"
                  >
                    {item.title}
                    <br />
                    {dataInfo.name}
                  </ReactTooltip>
                </div>
                <Line
                  percent={item.percent}
                  strokeWidth={2.2}
                  trailWidth={2.2}
                  strokeColor={getColor(itemNumber)}
                  trailColor="rgba(21, 24, 29, 0.75)"
                  className={styles.progressLine}
                />
              </div>
            ))}
        </div>
      </div>
    );
  }
}

export default DetailedLine;
