import React, { PureComponent } from "react";
import ReactDOM from "react-dom";
import { Provider } from "mobx-react";

import App from "./components/App";
import AppStore from "./stores/AppStore";

type AppWrapperProps = {};

class AppWrapper extends PureComponent<AppWrapperProps> {
  private AppStore: AppStore = new AppStore();

  get stores(): object {
    return {
      AppStore: this.AppStore,
      ProductTraceabilityStore: this.AppStore.ProductTraceabilityStore,
      MapStore: this.AppStore.MapStore,
      ParticipantsStore: this.AppStore.ParticipantsStore,
      GroupProductStore: this.AppStore.GroupProductStore,
      CountriesStore: this.AppStore.CountriesStore,
      PeriodsStore: this.AppStore.PeriodsStore,
    };
  }

  render() {
    return (
      <Provider {...this.stores}>
        <App />
      </Provider>
    );
  }
}

ReactDOM.render(<AppWrapper />, document.getElementById("root"));
