import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import { PieChart } from "react-minimal-pie-chart";

import styles from "./Risks.module.scss";
import Icon from "../Icon";
import { ReactComponent as DropdownIcon } from "./assets/dropdown.svg";
import AppStore from "../../stores/AppStore";

type RisksProps = {
  AppStore?: AppStore;
};

/**
 *  - 
 */
@inject("AppStore")
@observer
class Risks extends PureComponent<RisksProps> {
  render() {
    const { AppStore } = this.props;
    const { risks } = AppStore.data;

    const preparedData = risks.map((risk) => {
      return risk.withRisks
        ? {
            title: " ",
            color: "#FF6B00",
            percent: risk.percent,
            value: risk.percent,
          }
        : {
            title: " ",
            color: "#21931F",
            percent: risk.percent,
            value: risk.percent,
          };
    });

    return (
      <div className={styles.container}>
        <div className={styles.containerTitle}>
          <span className={styles.title}></span>
        </div>
        <div className={styles.containerChart}>
          <PieChart data={preparedData} radius={35} segmentsShift={1} />
        </div>
        <div className={styles.containerLegend}>
          {preparedData.map((item: any, index: any) => (
            <div key={index} className={styles.legend}>
              <div className={styles.legendName}>
                <div className={styles.containerIcons}>
                  <Icon
                    name="circle"
                    fill={item.color}
                    width={10}
                    height={10}
                    className={styles.circle}
                  />
                  <Icon
                    name="ellipse"
                    fill={item.color}
                    width={18}
                    height={18}
                    className={styles.ellipse}
                  />
                </div>
                {item.title}
              </div>
              <div className={styles.show}>
                <span className={styles.legendDescription}>{item.percent}</span>
                <DropdownIcon className={styles.iconDropdown} />
              </div>
            </div>
          ))}
        </div>
      </div>
    );
  }
}

export default Risks;
