import { configure, observable, action, decorate } from "mobx";
import AppStore from "./AppStore";

configure({ enforceActions: "observed" });

class GroupProductStore {
  appStore: AppStore;

  constructor(appStore: AppStore) {
    this.appStore = appStore;
  }

  openedGroup: string | null = null;

  openGroup = (group): void => {
    this.openedGroup = group;
    this.appStore.setParams([["tnved_group", group]]);
  };

  closeGroup = (): void => {
    this.openedGroup = null;
    this.appStore.setParams([["tnved_group", null]]);
  };

  getColor = (number: number): string => {
    switch (number) {
      case 0:
        return "#b658f5";
      case 1:
        return "#29cc97";
      case 2:
        return "#fcc300";
      case 3:
        return "#44c8f5";
      default:
        return "#44c8f5";
    }
  };

  resetFilters = () => {
    this.openedGroup = null;
  };
}

decorate(GroupProductStore, {
  openedGroup: observable,
  openGroup: action,
  closeGroup: action,
});

export default GroupProductStore;
