import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import styles from "./Countries.module.scss";
import DetailedCountries from "./components/DetailedCountries";

import CountriesStore from "../../stores/CountriesStore";

type CountriesProps = {
  CountriesStore?: CountriesStore;
};

type CountriesState = {
  dataInfo: any;
};

/**
 *  -  
 */
@inject("CountriesStore")
@observer
class Countries extends PureComponent<CountriesProps, CountriesState> {
  render() {
    const { CountriesStore } = this.props;
    const {
      filterAll,
      filterEAES,
      showAllCountries,
      showEAESCountries,
    } = CountriesStore;

    return (
      <div className={styles.container}>
        <div className={styles.containerTitle}>
          <span className={styles.title}>
            
            <br />
            
          </span>
          <div className={styles.containerChooseCountries}>
            <div className={styles.containerCountries}>
              <span
                onClick={showEAESCountries}
                className={filterEAES ? styles.activeSpan : ""}
              >
                
              </span>
              <span
                onClick={showAllCountries}
                className={filterAll ? styles.activeSpan : ""}
              >
                
              </span>
            </div>
          </div>
        </div>
        <div>
          <DetailedCountries />
        </div>
      </div>
    );
  }
}

export default Countries;
