import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";
import { Line } from "rc-progress";
// @ts-ignore
import ReactCountryFlag from "react-country-flag";

import { ReactComponent as DropdownIcon } from "../../assets/dropdown.svg";
import { ReactComponent as BackIcon } from "../../assets/back.svg";

import styles from "./DetailedCountries.module.scss";

import AppStore from "../../../../stores/AppStore";
import CountriesStore from "../../../../stores/CountriesStore";

type DetailedCountriesProps = {
  AppStore?: AppStore;
  CountriesStore?: CountriesStore;
};

/**
 *  -  
 */
@inject("CountriesStore", "AppStore")
@observer
class DetailedCountries extends PureComponent<DetailedCountriesProps> {
  render() {
    const { AppStore, CountriesStore } = this.props;
    const { isOpen, setOpen, setClose } = CountriesStore;

    const { countries } = AppStore.data;

    const components = [
      {
        title: "",
        percent: 11,
        code: "BR",
      },
      {
        title: "",
        percent: 6,
        code: "CA",
      },
      {
        title: "",
        percent: 4,
        code: "HU",
      },
      {
        title: "",
        percent: 1.15,
        code: "JP",
      },
    ];

    return (
      <div>
        {countries.map((item: any, index: any) => (
          <div key={item.code} className={styles.containerProgressBarLines}>
            <div className={styles.containerProgressIcon}>
              <div className={styles.containerProgressTitle}>
                <span className={styles.progressBarTitle}>{item.name}</span>
                <span className={styles.progressBarCost}>{item.percent}%</span>
              </div>
              {item.code === "ANY_CODE" && !isOpen && (
                <DropdownIcon
                  className={styles.iconDropdown}
                  onClick={setOpen}
                />
              )}
              {item.code === "ANY_CODE" && isOpen && (
                <BackIcon className={styles.iconDropdown} onClick={setClose} />
              )}
              {item.code !== "ANY_CODE" && item.code !== "--" && (
                <ReactCountryFlag
                  countryCode={item.code}
                  svg
                  style={{
                    width: "25px",
                    height: "23px",
                  }}
                  className={styles.iconCountry}
                />
              )}
            </div>
            <Line
              percent={item.percent}
              strokeWidth={2.2}
              trailWidth={2.2}
              strokeColor="#023AFF"
              trailColor="rgba(21, 24, 29, 0.75)"
              className={styles.progressLine}
            />

            {isOpen &&
              item.code === "ANY_CODE" &&
              components.map((country: any, index: any) => (
                <div
                  key={country.code}
                  className={styles.containerProgressBarLinesDetailed}
                >
                  <div className={styles.containerProgressIcon}>
                    <div className={styles.containerProgressTitle}>
                      <span className={styles.progressBarTitle}>
                        {country.title}
                      </span>
                      <span className={styles.progressBarCost}>
                        {country.percent}%
                      </span>
                    </div>
                    <ReactCountryFlag
                      countryCode={country.code}
                      svg
                      style={{
                        width: "25px",
                        height: "23px",
                      }}
                      className={styles.iconCountry}
                    />
                  </div>
                  <Line
                    percent={country.percent}
                    strokeWidth={2.2}
                    trailWidth={2.2}
                    strokeColor="#023AFF"
                    trailColor="rgba(21, 24, 29, 0.75)"
                    className={styles.progressLine}
                  />
                </div>
              ))}
          </div>
        ))}
      </div>
    );
  }
}

export default DetailedCountries;
