import React, { PureComponent } from "react";
import { observer, inject } from "mobx-react";

import styles from "./DiagramsMain.module.scss";

import ProductTraceability from "../ProductTraceability";
import IncludeTraceability from "../IncludeTranceability";
import LeaveTraceability from "../LeaveTraceability";
import Participants from "../Participants";
import DetailedParticipants from "../DetailedParticipants";
import GroupProduct from "../GroupProduct";
import Countries from "../Countries";
import Risks from "../Risks";

import AppStore from "../../stores/AppStore";
import ProductTraceabilityStore from "../../stores/ProductTraceabilityStore";
import ParticipantsStore from "../../stores/ParticipantsStore";

type DiagramsMainProps = {
  ProductTraceabilityStore?: ProductTraceabilityStore;
  AppStore?: AppStore;
  ParticipantsStore?: ParticipantsStore;
};

type DiagramsMainState = {
  OSN: any;
  SRN: any;
};

/**
 *    
 */

@inject("AppStore", "ProductTraceabilityStore", "ParticipantsStore")
@observer
class DiagramsMain extends PureComponent<DiagramsMainProps, DiagramsMainState> {
  constructor(props: DiagramsMainProps) {
    super(props);

    this.state = {
      OSN: {
        title: "  ",
        value: "897 564",
        importers: "546 056",
        importersPercent: 60,
        exporters: "356 154",
        exportersPercent: 40,
        insideTurn: "389 465",
        insideTurnPercent: 42,
        consumers: "508 646",
        consumersPercent: 55,
      },
      SRN: {
        title: "  ",
        value: "568 315",
        importers: "546 056",
        importersPercent: 60,
        exporters: "356 154",
        exportersPercent: 40,
        insideTurn: "389 465",
        insideTurnPercent: 42,
        consumers: "508 646",
        consumersPercent: 55,
      },
    };
  }

  render() {
    const {
      state: { OSN, SRN },
    } = this;

    const {
      AppStore,
      ParticipantsStore,
      ProductTraceabilityStore,
    } = this.props;
    const { includeTraceability, leaveTraceability } = ProductTraceabilityStore;
    const { OSNcheck, SRNcheck } = ParticipantsStore;

    return (
      <div className={styles.container}>
        {!includeTraceability && !leaveTraceability ? (
          <ProductTraceability />
        ) : (
          (includeTraceability && <IncludeTraceability />) ||
          (leaveTraceability && <LeaveTraceability />)
        )}

        {!OSNcheck && !SRNcheck ? (
          <Participants />
        ) : (
          <DetailedParticipants dataInfo={OSNcheck ? OSN : SRN} />
        )}
        <div>
          <div className={styles.containerGroupAndCountires}>
            <GroupProduct />
            <Countries />
          </div>
        </div>
        {AppStore.data.risks ? <Risks /> : null}
      </div>
    );
  }
}

export default DiagramsMain;
