import { configure, observable, action, decorate } from "mobx";
import AppStore from "./AppStore";

configure({ enforceActions: "observed" });

class ProductTraceabilityStore {
  appStore: AppStore;

  constructor(appStore: AppStore) {
    this.appStore = appStore;
  }

  includeTraceability: boolean = false;
  leaveTraceability: boolean = false;

  changeIncludeTraceability = (): void => {
    this.includeTraceability = !this.includeTraceability;
    this.appStore.setParams([
      ["indicator", this.includeTraceability ? "Import" : null],
    ]);
  };

  changeLeaveTraceability = (): void => {
    this.leaveTraceability = !this.leaveTraceability;
    this.appStore.setParams([
      ["indicator", this.leaveTraceability ? "Export" : null],
    ]);
  };

  resetFilters = () => {
    this.includeTraceability = false;
    this.leaveTraceability = false;
  };
}

decorate(ProductTraceabilityStore, {
  includeTraceability: observable,
  leaveTraceability: observable,
  changeIncludeTraceability: action,
  changeLeaveTraceability: action,
});

export default ProductTraceabilityStore;
